import * as React from "react";
import {withStyles} from "@material-ui/core";
import classNames from 'classnames';

const styles = {
    root: {
        display: 'flex',
    },
    directionRow: {
        flexDirection: 'row',
    },
    directionColumn: {
        flexDirection: 'column',
    },
    alignItemsStart: {
        alignItems: 'flex-start',
    },
    alignItemsEnd: {
        alignItems: 'flex-end',
    },
    alignItemsCenter: {
        alignItems: 'center',
    },
    alignItemsStretch: {
        alignItems: 'stretch',
    },
    justifyStart: {
        justifyContent: 'flex-start',
    },
    justifyEnd: {
        justifyContent: 'flex-end',
    },
    justifyCenter: {
        justifyContent: 'center',
    },
    justifySpaceBetween: {
        justifyContent: 'space-between',
    },
    justifySpaceEvenly: {
        justifyContent: 'space-evenly',
    },
    grow: {
        flexGrow: 1,
    },
    wrap: {
        flexWrap: 'wrap',
    },
    fullWidth: {
        width: '100%',
    },
};

class Flex extends React.Component {
    render() {

        const {classes, alignItems, direction, justify, grow, wrap, fullWidth, children, className, ...other} = this.props;
        const cssClasses = classNames(
            classes.root,
            className,
            {
                [classes.directionRow]: direction === 'row',
                [classes.directionColumn]: direction === 'column',
                [classes.alignItemsStart]: alignItems === 'flex-start',
                [classes.alignItemsEnd]: alignItems === 'flex-end',
                [classes.alignItemsCenter]: alignItems === 'center',
                [classes.alignItemsStretch]: alignItems === 'stretch',
                [classes.justifyStart]: justify === 'flex-start',
                [classes.justifyEnd]: justify === 'flex-end',
                [classes.justifyCenter]: justify === 'center',
                [classes.justifySpaceBetween]: justify === 'space-between',
                [classes.justifySpaceEvenly]: justify === 'space-evenly',
                [classes.grow]: grow,
                [classes.wrap]: wrap,
                [classes.fullWidth]: fullWidth,
            }
        );

        return (
            <div className={cssClasses} {...other}>
                {children}
            </div>
        )
    }
}

Flex.defaultProps = {
    direction: 'row',
    alignItems: 'flex-start',
    justify: 'flex-start',

};


export default withStyles(styles)(Flex)
