import React from "react";
import getContentPerTypeService from "./content-converter-service";
import DisplayImageLegend from "./display-image-legend";

export default class DisplayImage extends React.Component{
    constructor(props) {
        super(props);

        /* Pour ajouter de nouveaux éléments dans les images, il suffit de les ajouter à la suite de ce tableau
         * puis de créer le composant correspondant dans le render */
        this.typeList = ['src', 'legend'];
    }

    render() {

        const contentPerType = getContentPerTypeService(this.typeList, this.props.content);

        return (
            <React.Fragment>
                {contentPerType.map((contentPT, index) => {
                    return (() => {
                        switch (contentPT.type) {
                            case this.typeList[0]:
                                return (
                                    <div key={index} className='mt-2'>
                                        <a href={contentPT.value} target="_blank" rel="noopener noreferrer">
                                            <img src={contentPT.value} alt=""/>
                                        </a>
                                    </div>
                                );
                            case this.typeList[1]:
                                return <DisplayImageLegend key={index} content={contentPT.value}/>;
                            default:
                                return null;
                                // Pas de defaut dans une image
                        }
                    })();
                })}
            </React.Fragment>
        )
    }
}