import * as React from "react";
import Link from "react-router-dom/es/Link";
import Typography from "@material-ui/core/Typography/Typography";
import getThemeService from "../common/get-theme-service";
import SiteService from "../common/site-service";
import Icon from "@material-ui/core/Icon/Icon";
import ConditionalLink from "../common/conditional-link";
import baseBackendUrl from "../common/base-backend-url";
import Flex from "../common/flex";
import SnackbarsExtended from "../common/snackbar-extended";


const axios = require('axios');


export default class DisplayFolderBrothers extends React.Component {
    constructor(props) {
        super(props);
        this.state = {isChild: !!this.props.article.parent, lastArticle: null, nextArticle: null, error: false}
    }

    componentDidMount() {
        if (!this.state.isChild) {
            return;
        }

        this.getBrothers()

    }

    getBrothers = async () => {

        try {
            const response = await axios.get(baseBackendUrl.getBaseBackendUrl() + "/articles/" + this.props.article.urlId + '/brothers');

            const prevArticle = response.data.find((article) => {
                return article.order === this.props.article.order - 1;
            });
            const nextArticle = response.data.find((article) => {
                return article.order === this.props.article.order + 1;
            });

            this.setState({prevArticle: prevArticle, nextArticle: nextArticle});
        } catch (error) {
            this.setState({error: true});
        }
    };

    handleCloseSnakbar = () => {
        this.setState({error: false});
    };

    render() {

        const { isChild, error, prevArticle, nextArticle } = this.state;
        const { article } = this.props;

        if (!isChild) {
            return null;
        }

        const theme = getThemeService(SiteService.getSite());

        const colorLink = {
            color: theme.palette.primary.main
        };

        return (
            <div className="folder-brother-list">
                <hr/>
                Cet article est contenu dans un dossier :
                <div className="folder-name">
                    <Link style={colorLink} to={`/articles/${article.parent.urlId}`}>
                        <Typography variant="subtitle1">
                            {article.parent.title}
                        </Typography>
                    </Link>
                </div>
                <div className="brother-list">
                    <Flex justify='space-between' alignItems='center'>
                        <ConditionalLink condition={prevArticle} to={prevArticle ? prevArticle.urlId : null}>
                            <Flex justify='space-between' alignItems='center'>
                                <Icon>arrow_back</Icon>
                                <Typography
                                    variant="button">{prevArticle ? prevArticle.title : 'Pas d\'article précédent'}</Typography>
                            </Flex>
                        </ConditionalLink>
                        <ConditionalLink condition={nextArticle} to={nextArticle ? nextArticle.urlId : null}>
                            <Flex justify='space-between' alignItems='center'>
                                <Typography
                                    variant="button">{nextArticle ? nextArticle.title : 'Pas d\'article suivant'}</Typography>
                                <Icon>arrow_forward</Icon>
                            </Flex>
                        </ConditionalLink>
                    </Flex>
                </div>
                <hr/>
                <SnackbarsExtended open={error} variant='error' onClose={this.handleCloseSnakbar}
                                   message='Une erreur est survenue, veuillez recharger la page'/>
            </div>
        );
    }

}