import React from "react";
import getContentPerTypeService from "../details-article/content/content-converter-service";
import DisplayLink from "../details-article/content/display-link";
import Iframe from "../details-article/content/display-iframe";
import {Typography} from "@material-ui/core";
import LinkContinueDescription from './link-continue-description'

export default class DisplayParagraph extends React.Component{
    constructor(props) {
        super(props);

        /* Pour ajouter de nouveaux éléments dans les paragraphes, il suffit de les ajouter à la suite de ce tableau
         * puis de créer le composant correspondant dans le render */
        this.typeList = ['strong', 'i', 'a', 's', 'u', 'iframe'];
    }

    render() {

        const {content, isDescription} = this.props;

        const contentPerType = getContentPerTypeService(this.typeList, content);

        return (
            <Typography variant='body1' id={this.props.content.substring(0,20).replace(/ /g, '-').replace(/[àéèùç:]/g, '')} className={this.props.className}>
                {contentPerType.map((contentPT, index) => {
                    return (() => {
                        switch (contentPT.type) {
                            case this.typeList[0]:
                                return <strong key={index}>{contentPT.value}</strong>;
                            case this.typeList[1]:
                                return <i key={index}>{contentPT.value}</i>;
                            case this.typeList[2]:
                                return <DisplayLink content={contentPT.value} key={index}/>;
                            case this.typeList[3]:
                                return <s key={index}>{contentPT.value}</s>;
                            case this.typeList[4]:
                                return <u key={index}>{contentPT.value}</u>;
                            case this.typeList[5]:
                                return <Iframe key={index}>{contentPT.value}</Iframe>;
                            default: // default correspont au texte de base du paragraphe :)
                                return contentPT.value;
                        }
                    })();
                })}
                {isDescription && (
                  <LinkContinueDescription />
                )}
            </Typography>
        )
    }
}