import React from 'react'
import ReactDisqusComments from 'react-disqus-comments'
import { Button } from '@material-ui/core'
import Icon from '@material-ui/core/Icon'
import withStyles from '@material-ui/core/styles/withStyles'

const style = {
  button: {
    width: '70%',
    marginBottom: '1rem'
  }
}

class DisplayArticleComments extends React.Component {

  constructor(props) {
    super(props)
    this.state = {showDisqus: false}
  }

  handleShowComments = () => {
    this.setState((prevState) => ({showDisqus: !prevState.showDisqus}))
  }

  render() {
    const {article, classes} = this.props
    const {showDisqus} = this.state

    const url = 'https://www.sup-vision.infos.st/articles/' + article.id

    const identifier = (article.shareComments && article.parent && article.parent.shareComments) ?
      article.parent.id + '' : article.id + ''

    return (
      <div>
        <Button variant='contained' color='primary' onClick={this.handleShowComments} className={classes.button}>
          {!showDisqus ? 'Afficher' : 'Cacher'} les commentaires
          {
            !showDisqus ? (
              <Icon>arrow_drop_down</Icon>
            ) : (
              <Icon>arrow_drop_up</Icon>
            )
          }
        </Button>
        {showDisqus && (
          <ReactDisqusComments
            shortname="sup-vision-infos-st.disqus.com/embed.js"
            identifier={identifier}
            title="test"
            url={url}/>
        )}
      </div>
    )
  }
}

export default withStyles(style)(DisplayArticleComments)