import {BrowserRouter as Router, Link, Redirect, Route, Switch} from "react-router-dom";
import AppBar from "@material-ui/core/AppBar/AppBar";
import Toolbar from "@material-ui/core/Toolbar/Toolbar";
import FilterableListeArticles from "../liste-articles/filterable-liste-articles";
import DetailsArticle from "../details-article/details-article";
import React from "react";
import Typography from "@material-ui/core/Typography/Typography";
import {withStyles} from "@material-ui/core";
import PropTypes from 'prop-types';

const styles = {
    siteDescription: {
        marginLeft: 10,
    },
};
class DisplayAppContent extends React.Component {

    render() {

        const {classes} = this.props;

        return (
            <Router>
                <div className="app-content">
                    <AppBar position="static" color="primary">
                        <Toolbar>
                            <Link to="/">
                                <img src={"https://supviz.infos.st/" + this.props.logoSrc} className="header-logo" alt=""/>
                            </Link>
                            <Typography className={classes.siteDescription} color="inherit" variant="body2">
                                {this.props.siteDescription}
                            </Typography>
                        </Toolbar>
                    </AppBar>

                    <div className="contenu">
                        <Route path="/" component={routeHasChanged} />
                        <Switch>
                            <Route exact path="/" component={FilterableListeArticles}/>
                            <Route exact path="/articles/:urlId" component={DetailsArticle}/>
                            <Route exact path="/:tag" component={FilterableListeArticles}/>
                            <Route component={redirect} />
                        </Switch>
                    </div>
                </div>
            </Router>
        )
    }
}

DisplayAppContent.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(DisplayAppContent);

const routeHasChanged = () => {

    window.location.hash = window.decodeURIComponent(window.location.hash);
    const { hash } = window.location;

    if (hash !== '') {

        setTimeout(() => {
            const id = hash.replace('#', '');
            const element = document.querySelector(`#${id}`);

            if (element) {
                element.scrollIntoView({behavior: 'smooth', block: 'start'});
            }
        }, 1000);
    } else {
        window.scrollTo(0,0);
    }

    return null;
};

const redirect = () => {
    return (
        <Redirect to='/'/>
    )
};