import React from "react";
import DisplayParagraph from "../../common/display-paragraph";
import DisplayH2 from "./display-h2";
import DisplayImage from "./display-image";
import getContentPerTypeService from "./content-converter-service"

export default class DisplayArticleComponents extends React.Component {

    constructor(props) {
        super(props);

        /* Pour ajouter de nouveaux éléments à la racine de l'article, il suffit de les ajouter à la suite de ce tableau
         * puis de créer le composant correspondant dans le render */
        this.typeList = ['h2', 'p', 'img'];
    }

    render() {

       const contentPerType = getContentPerTypeService(this.typeList, this.props.content);
        return (
            <div className='article-content-text'>
                {contentPerType.map((contentPT, index) => {
                    return (() => {
                        switch (contentPT.type) {
                            case this.typeList[0]:
                                return <DisplayH2 content={contentPT.value} key={index}/>;
                            case this.typeList[1]:
                                return <DisplayParagraph content={contentPT.value} key={index}/>;
                            case this.typeList[2]:
                                return <DisplayImage content={contentPT.value} key={index}/>;
                            default:
                                return null;
                                // Pas de default ici car il ne peut y avoir de texte sans élément à la racine de l'article (c'est soit un titre, une citation, un paragraphe etc)
                        }
                    })();
                })}
            </div>
        );
    }
}