import React from "react";

export default class Iframe extends React.Component{

    render() {

        let variableAttribute = {};

        this.props.split(' ').forEach((prop) => {
            const [variablePropName, variablePropValue] = prop.split('=');
            variableAttribute = Object.assign(variableAttribute, { [variablePropName]: variablePropValue });
        });

        return (
            <iframe title="video" {...variableAttribute} />
        )
    }
}