import * as React from "react";
import Typography from "@material-ui/core/Typography/Typography";


export default class DisplayPaypalDonationButton extends React.Component {


    render() {

        return (
            <div>
                <Typography variant="body2">
                    Ce site ne comporte aucune pub et est maintenu bénévolement. Si vous souhaitez me soutenir, offrez moi une baguette (soit 1€) !
                    Et en plus ça vous prendra moins d'une minute :)
                </Typography>
                <form action="https://www.paypal.com/cgi-bin/webscr" method="post" target="_top">
                    <input type="hidden" name="cmd" value="_s-xclick"/>
                    <input type="hidden" name="hosted_button_id" value="7H4GSHNZNEGLU"/>
                    <input type="image" src="https://www.paypalobjects.com/fr_FR/FR/i/btn/btn_donate_LG.gif" border="0" name="submit" alt="PayPal, le réflexe sécurité pour payer en ligne"/>
                    <img alt="" border="0" src="https://www.paypalobjects.com/fr_FR/i/scr/pixel.gif" width="1" height="1"/>
                </form>
            </div>
        );
    }
}