import Cookies from 'universal-cookie';

export default class CookieService {

    static maxAge = 60; // days
    static maxAgeGdpr = 360; // days

    static cookies = new Cookies();

    static getCookie(key) {
        return this.cookies.get(key) || '';
    }

    static setCookie({key, value}, gdpr = false) {

        const expirationDate = new Date();
        expirationDate.setTime(expirationDate.getTime() + (3600 * 1000 * 24  * (gdpr ? this.maxAgeGdpr : this.maxAge)));

        this.cookies.set(key, btoa(value), {path: '/', expires : expirationDate});
    }
}