import React from "react";
import DisplayArticleVotes from "./display-article-votes";
import DisplayArticleTagList from "./display-article-tag-list";


export default class DisplayArticleFooter extends React.Component {

    render() {
        const article = this.props.article;

        return (
            <div className="footer">
                <DisplayArticleVotes article={article}/>

                <DisplayArticleTagList article={article}/>
            </div>
        );
    }
}