import React from "react";
import getContentPerTypeService from "./content-converter-service";
import Link from "react-router-dom/es/Link";
import SiteService from "../../common/site-service";
import getThemeService from "../../common/get-theme-service";

export default class DisplayLink extends React.Component {
    constructor(props) {
        super(props);

        /* Pour ajouter de nouveaux éléments dans les liens, il suffit de les ajouter à la suite de ce tableau
         * puis de créer le composant correspondant dans le render */
        this.typeList = ['href', 'text'];
    }

    render() {
        const contentPerType = getContentPerTypeService(this.typeList, this.props.content);

        if (!contentPerType[0]) {
            return '';
        }

        const site = SiteService.getSite();

        const hrefContent = contentPerType.find((contentPerType) => {
            return contentPerType.type === this.typeList[0];
        });
        const textContent = contentPerType.find((contentPerType) => {
            return contentPerType.type === this.typeList[1];
        });

        const theme = getThemeService(site);

        const colorLink = {
            color: theme.palette.primary.main
        };

        const internalLink = hrefContent.value.indexOf(site + '.sup-vision.infos.st/articles');

        if (internalLink === -1) {

            return (
                <a style={colorLink} href={hrefContent.value} target="_blank" rel="noopener noreferrer">
                    {textContent ? textContent.value : null}
                </a>
            );
        } else {
            return (
                <Link style={colorLink}
                      to={'/articles/' + hrefContent.value.substring(('https://' + site + '.sup-vision.infos.st/articles/').length)}
                      target="_blank" rel="noopener noreferrer">
                    {textContent ? textContent.value : null}
                </Link>
            );

        }

    }
}