import React from 'react';
import Button from '@material-ui/core/Button';
import Icon from '@material-ui/core/Icon';
import SearchTags from "./search-tags";
import baseBackendUrl from "../common/base-backend-url"
import CircularProgress from "@material-ui/core/CircularProgress/CircularProgress";
import FormControlLabel from "@material-ui/core/FormControlLabel/FormControlLabel";
import Switch from "@material-ui/core/Switch/Switch";
import Typography from "@material-ui/core/Typography/Typography";
import SnackbarsExtended from "../common/snackbar-extended";

const axios = require('axios');

export default class SearchArticles extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            tagList: null,
            selectedTagList: this.props.selectedTag,
            sortDir: false,
            sortProp: 'creationDate',
            show_folders: true,
            show_children: true,
            error: false
        };
        this.props.onFormChange(this.createUrlFromState());

        this.getAllTags();
    }


    componentWillReceiveProps(newProps, nextContext) {

        if (newProps.selectedTag !== this.props.selectedTag) {
            this.setState({
                selectedTagList: newProps.selectedTag
            }, () => {
                this.props.onFormChange(this.createUrlFromState());
            });
        }
    }

    getAllTags = async () => {

        try {
            const response = await axios.get(baseBackendUrl.getBaseBackendUrl() + "/tags?" + baseBackendUrl.getSiteParam());
            this.setState({tagList: response.data});
        } catch (error) {
            this.setState({error: true});
        }
    };

    createUrlFromState() {
        let searchParamsUrl = '?';

        const sortConverter = {
            true: 'asc',
            false: 'desc'
        };

        searchParamsUrl += "tags=" + this.state.selectedTagList.join(";");
        searchParamsUrl += "&sortDir=" + sortConverter[this.state.sortDir] + "&sortProp=" + this.state.sortProp;
        searchParamsUrl += "&showFolders=" + this.state.show_folders + "&showChildren=" + this.state.show_children;

        return searchParamsUrl;
    }

    handleButtonClick(e, btn) {
        this.setState((prevState, props) => ({
            sortProp: btn,
            sortDir: prevState.sortProp === btn ? !prevState.sortDir : true
        }), () => {
            this.props.onFormChange(this.createUrlFromState());
        });
    }

    handleCheckboxClick(e, chk) {

        this.setState((prevState, props) => ({
            ['show_' + chk]: !prevState['show_' + chk],
        }), () => {
            this.props.onFormChange(this.createUrlFromState());
        });
    }

    handleCloseSnakbar = () => {
       this.setState({error: false});
    };

    onSelectedTagListChange = (e) => {

        this.setState({selectedTagList: e.map((tag) => tag.label)}, () => {
            this.props.onFormChange(this.createUrlFromState());
        });
    };

    render() {

        const { tagList, selectedTagList, sortProp, sortDir, show_folders, show_children, error } = this.state;

        const convertSortDirIcon = {
            true: 'up',
            false: 'down'
        };

        if (!tagList) {
            return (
                <div className="search-bar">
                    <CircularProgress color="primary"/>
                    <SnackbarsExtended open={error} variant='error' onClose={this.handleCloseSnakbar}
                                       message='Une erreur est survenue, veuillez recharger la page'/>
                </div>
            );
        } else {
            return (
                <div className="search-bar">
                    <div className="search-tags">
                        <SearchTags tagList={tagList.map((tag) => ({
                                        label: tag.label,
                                        value: tag.label
                                    }))}
                                    onSelectedTagListChange={(e) => this.onSelectedTagListChange(e)}
                                    selectedTagList={selectedTagList}/>
                    </div>
                    <Button color="primary" variant="text" onClick={(e) => this.handleButtonClick(e, 'creationDate')}>
                        <Icon color="secondary" style={{fontSize: 40}}>
                            {'arrow_drop_' + (sortProp === 'creationDate' ? convertSortDirIcon[sortDir] : 'up')}
                        </Icon>
                        <span className="search-bar-icon">
                            <Icon color="primary" className="icon-miniature">
                                calendar_today
                            </Icon>
                            &nbsp;Date
                        </span>
                    </Button>
                    <Button color="primary" variant="text"
                            onClick={(e) => this.handleButtonClick(e, 'modificationDate')}>
                        <Icon color="secondary" style={{fontSize: 40}}>
                            {'arrow_drop_' + (sortProp === 'modificationDate' ? convertSortDirIcon[sortDir] : 'up')}
                        </Icon>
                        <span className="search-bar-icon">
                            <Icon color="primary" className="icon-miniature">
                                update
                            </Icon>
                            &nbsp;Mise à jour
                        </span>
                    </Button>
                    <Button color="primary" variant="text" onClick={(e) => this.handleButtonClick(e, 'views')}>
                        <Icon color="secondary" style={{fontSize: 40}}>
                            {'arrow_drop_' + (sortProp === 'views' ? convertSortDirIcon[sortDir] : 'up')}
                        </Icon>
                        <span className="search-bar-icon">
                            <Icon color="primary" className="icon-miniature">
                                visibility
                            </Icon>
                            &nbsp;Vues
                        </span>
                    </Button>
                    <Button color="primary" variant="text" onClick={(e) => this.handleButtonClick(e, 'upvotes')}>
                        <Icon color="secondary" style={{fontSize: 40}}>
                            {'arrow_drop_' + (sortProp === 'upvotes' ? convertSortDirIcon[sortDir] : 'up')}
                        </Icon>
                        <span className="search-bar-icon">
                            <Icon color="primary" className="icon-miniature">
                                thumb_up
                            </Icon>
                            &nbsp;Votes
                        </span>
                    </Button>

                    <FormControlLabel
                        control={
                            <Switch checked={show_folders}
                                    color="secondary"
                                    onChange={(e) => this.handleCheckboxClick(e, 'folders')}/>
                        }
                        label={
                            <span className="search-bar-icon">
                                <Icon color="primary" className="icon-miniature">
                                    folder
                                </Icon>
                                <Typography color="primary" variant="button">
                                    &nbsp;Dossiers
                                </Typography>
                            </span>
                        }
                    />
                    <FormControlLabel
                        control={
                            <Switch checked={show_children}
                                    color="secondary"
                                    onChange={(e) => this.handleCheckboxClick(e, 'children')}/>
                        }
                        label={
                            <span className="search-bar-icon">
                                    <Icon className="icon-miniature" color="primary">
                                        folder_open
                                    </Icon>
                                    <Typography color="primary" variant="button">
                                        &nbsp;Articles des dossiers
                                    </Typography>
                                </span>
                        }
                    />
                </div>
            );
        }
    }
}
