
import SiteService from './site-service'

function initBaseBackendUrl() {
    return (getEnv() === 'dev') ?
        'http://localhost:3030/api' :
        'https://sup-vision.infos.st:443/api'
}

function initSiteParam() {
    const site = SiteService.getSite();

    return site === 'supra' ?
                'site=2' :
            site === 'super' ?
                'site=1' :
            site === 'supple' ?
                'site=0' : '';
}


function getEnv() {
    return (window.location.href.indexOf('localhost')) !== -1 ? 'dev' : 'prod'
}

export default class baseBackendUrl {

    static backendUrl = initBaseBackendUrl();
    static siteParam = initSiteParam();

    static getBaseBackendUrl () {
        return this.backendUrl;
    }
    static getSiteParam () {
        return this.siteParam;
    }
}
