import React from "react";
import Typography from "@material-ui/core/Typography/Typography";

export default class DisplayH2 extends React.Component{

    render() {
        return (
            <Typography color="secondary" variant="h2" id={this.props.content.substring(0,20).replace(/ /g, '-').replace(/[àéèùç:]/g, '')}>
                {this.props.content}
            </Typography>
        )
    }
}