import React from 'react';
import Typography from "@material-ui/core/Typography/Typography";
import {withStyles} from "@material-ui/core";
import PropTypes from 'prop-types';
import Toolbar from "@material-ui/core/Toolbar/Toolbar";
import AppBar from "@material-ui/core/AppBar/AppBar";

const styles = {
    title: {
        paddingTop: 50,
    },
    link: {
        textDecoration: 'none'
    },
    siteList: {
        marginTop: 30,
        'img': {
            padding: 50,
        },
        'span': {
            cursor: 'pointer',
        },
    },
    siteList1: {
        display: 'flex',
        justifyContent: 'space-evenly',
    },
    siteList2: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'space-evenly',

        '&> a': {
            padding: 10,
            '@media (max-height: 1000px)': {
                marginTop: 30,
            },
        },
    }
};
class DisplayAppParent extends React.Component {

    render() {
        const {classes} = this.props;

        return(
            <div className="app-parent">
                <AppBar position="static" color="primary">
                    <Toolbar>
                        <Typography color="inherit" variant="h4">Support-Vision</Typography>
                    </Toolbar>
                </AppBar>

                <Typography className={classes.title} variant="h4">
                    Bienvenue sur Support-Vision, le site qui vous aide à y voir plus clair !
                </Typography>
                <Typography className={classes.title} variant="h6">
                    Ce site est séparé en 3 parties, chacune à propos d'un thème différent.
                </Typography>

                <div className={classes.siteList}>
                    <div className={classes.siteList1}>
                        <a href='https://supple.sup-vision.infos.st' className={classes.link}>
                            <img src="https://supviz.infos.st/suppleNoir.png" alt=""/>
                            <Typography variant="body2">{this.props.sitesDescriptions['supple']}</Typography>
                        </a>
                    </div>
                    <div className={classes.siteList2}>
                        <a href='https://super.sup-vision.infos.st' className={classes.link}>
                            <img src="https://supviz.infos.st/superNoir.png" alt="" />
                            <Typography variant="body2">{this.props.sitesDescriptions['super']}</Typography>
                        </a>
                        <a href='https://supra.sup-vision.infos.st' className={classes.link}>
                            <img src="https://supviz.infos.st/supraNoir.png" alt="" />
                            <Typography variant="body2">{this.props.sitesDescriptions['supra']}</Typography>
                        </a>
                    </div>
                </div>
            </div>
        )
    }
}

DisplayAppParent.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(DisplayAppParent);