import Typography from "@material-ui/core/Typography/Typography";
import React from "react";
import SiteService from './site-service'
import getThemeService from './get-theme-service'
import { withStyles } from '@material-ui/core'

const styles = {
    footer: {
        padding: '0 1rem 8px',
    },
};

class DisplayAppFooter extends React.Component {

    render() {
        const {classes} = this.props;
        const site = SiteService.getSite();
        const theme = getThemeService(site);

        const inlineBackgroundColor = {
            backgroundColor: theme.palette.tertiary.main
        };

        return (
            <div>
                <hr/>
                <div className={classes.footer} style={inlineBackgroundColor}>
                    <Typography color="inherit" variant="caption">
                        <a href='https://sup-vision.infos.st'>
                            Support-Vision
                        </a> -
                        Nom de domaine géré gratuitement par&nbsp;
                        <a href='http://www.azote.org/' title='Nom de domaine' target='_blank' rel="noopener noreferrer">
                            azote.org
                        </a>
                        &nbsp;- Me retrouver :&nbsp;
                        <a href='https://www.facebook.com/SupportVision' target='_blank' rel='noopener noreferrer'>Facebook</a>
                        &nbsp;ou&nbsp;
                        <a href='https://twitter.com/SupViz' target='_blank' rel='noopener noreferrer'>Twitter</a>
                        &nbsp;- <a href='mailto:sup.supvision@gmail.com'>Contact</a> <br/>
                        Ce site et son contenu sont mis à disposition selon les termes de la Licence&nbsp;
                        <a rel="license noopener noreferrer" href="http://creativecommons.org/licenses/by-nc-sa/4.0/" target='_blank'>
                             Creative Commons (CC BY-NC-SA 4.0)
                        </a>.
                    </Typography>
                </div>
            </div>
        );
    }
}

export default withStyles(styles)(DisplayAppFooter)
