import React from "react";
import DisplayArticleContent from "./display-article-content";
import DisplayArticleFooter from "./display-article-footer";
import DisplayArticleComments from "./display-article-comments";
import DisplayFolderChildren from "./display-folder-children";
import DisplayFolderBrothers from "./display-folder-brothers";
import DisplayPaypalDonationButton from "./display-paypal-donation-button";
import withStyles from '@material-ui/core/styles/withStyles'

const style = {
  comments: {
    width: '100%',
    marginTop: '2rem'
  }
}

class DisplayArticleMain extends React.Component {

    render() {
        const { article, classes } = this.props;

        return (
            <div className="article">
                <div className="article-content">
                    <DisplayArticleContent article={article}/>
                    <DisplayFolderChildren article={article}/>
                    <DisplayFolderBrothers article={article}/>
                </div>
                <div className="article-footer">
                    <DisplayArticleFooter article={article}/>
                </div>
                <div className="donation-button">
                    <DisplayPaypalDonationButton />
                </div>
                <div className={classes.comments}>
                    <DisplayArticleComments article={article} />
                </div>
            </div>
        );
    }
}

export default withStyles(style)(DisplayArticleMain)