

export default function getThemeService(site) {

    const superTheme = {palette: {primary: {main: '#0d47a1'}, secondary: {main: '#0277bd'}, tertiary: {main: '#e4f1f9'}}};
    const supraTheme = {palette: {primary: {main: '#e56f07'}, secondary: {main: '#ffa000'}, tertiary: {main: '#fbefe3'}}};
    const suppleTheme = {palette: {primary: {main: '#5d4037'}, secondary: {main: '#8d6e63'}, tertiary: {main: '#eae4e1'}}};

    return (site === 'supple') ? suppleTheme :
        (site === 'supra') ? supraTheme : superTheme;
}
